export default "import {IsTypeEqual, typeAssert} from 'type-assertions';\nimport {authors, books, extractAuthorIdFromBook, getBookFromAuthor} from './index';\n\ntypeAssert<IsTypeEqual<string, ReturnType<typeof extractAuthorIdFromBook>>>();\ntypeAssert<IsTypeEqual<string, Parameters<typeof getBookFromAuthor>[0]>>();\ntypeAssert<IsTypeEqual<typeof authors, {id: string; name: string; age: number}[]>>();\ntypeAssert<IsTypeEqual<typeof books, {id: string; authorId: string; title: string; date: string}[]>>();\n\n// @ts-expect-error\ntypeAssert<IsTypeEqual<string | number, ReturnType<typeof extractAuthorIdFromBook>>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<string | number, Parameters<typeof getBookFromAuthor>[0]>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<typeof authors, {id: string | number; name: string; age: number}[]>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<typeof books, {id: string; authorId: string | number; title: string; date: string}[]>>();\n";