export default "import {IsTypeEqual, typeAssert} from 'type-assertions';\nimport {Author, Book, authors, books, extractAuthorIdFromBook, getBookFromAuthor} from './index';\n\ntypeAssert<IsTypeEqual<Author['id'], ReturnType<typeof extractAuthorIdFromBook>>>();\ntypeAssert<IsTypeEqual<Author['id'], Parameters<typeof getBookFromAuthor>[0]>>();\ntypeAssert<IsTypeEqual<typeof authors, {id: Author['id']; name: string; age: number}[]>>();\ntypeAssert<IsTypeEqual<typeof books, {id: Book['id']; authorId: Author['id']; title: string; date: string}[]>>();\n\n// @ts-expect-error\ntypeAssert<IsTypeEqual<string, ReturnType<typeof extractAuthorIdFromBook>>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<string, Parameters<typeof getBookFromAuthor>[0]>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<typeof authors, {id: string; name: string; age: number}[]>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<typeof books, {id: string; authorId: string; title: string; date: string}[]>>();\n// @ts-expect-error\ntypeAssert<IsTypeEqual<Author['id'], Book['id']>>();\n";